"use client";

import { useState } from "react";
import { ChevronDown, HelpCircle, Terminal } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

const faqs = [
  {
    question: "What materials are used in your frames?",
    answer:
      "We use high-quality acrylic, canvas, and photon-reactive materials to ensure maximum durability and aesthetic precision.",
  },
  {
    question: "Can I customize the size of the frame?",
    answer:
      "Yes, you can choose from preset modular sizes or input custom coordinate dimensions during the initialization process.",
  },
  {
    question: "How long does deployment take?",
    answer:
      "Orders undergo rapid prototyping within 2–3 business days and are deployed to your location within 5–7 business days.",
  },
  {
    question: "Do you offer returns or exchanges?",
    answer:
      "Yes, we provide a 7-day synchronization window for any units showing structural or cosmetic anomalies.",
  },
  {
    question: "How do I mount the frame?",
    answer:
      "Each unit includes specialized mounting protocols and hardware for a seamless integration with your interior infrastructure.",
  },
  {
    question: "Is the backlight adjustable?",
    answer:
      "Standard backlight intensity is calibrated for optimal visual fidelity. Variable photon nodes are scheduled for future upgrades.",
  },
];

export default function Faq() {
  const [openIndex, setOpenIndex] = useState(null);

  return (
    <section className="relative py-32 overflow-hidden">
      <div className="max-w-4xl mx-auto px-6 relative z-10">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="mb-16 text-center space-y-4"
        >
          <div className="inline-flex items-center gap-2 px-3 py-1 bg-primary/10 border border-primary/20 text-primary rounded-full text-[10px] font-black uppercase tracking-widest">
            <Terminal className="w-3 h-3" />
            <span>Support Database</span>
          </div>
          <h2 className="text-5xl md:text-7xl font-black text-secondary tracking-tighter uppercase whitespace-pre-line">
            System <span className="text-primary italic">Documentation</span>
          </h2>
          <p className="text-neutral-500 text-lg font-medium max-w-xl mx-auto">
            Access core intelligence regarding product specifications and deployment protocols.
          </p>
        </motion.div>

        <div className="space-y-4">
          {faqs.map((faq, index) => (
            <motion.div
              layout
              key={index}
              initial={{ opacity: 0, x: -20 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ delay: index * 0.1 }}
              className={`group overflow-hidden rounded-[2rem] border-2 transition-all duration-500 ${openIndex === index
                  ? "bg-white border-primary shadow-2xl shadow-primary/10"
                  : "bg-white/50 backdrop-blur-md border-neutral-100 hover:border-primary/30"
                }`}
            >
              <button
                onClick={() => setOpenIndex(openIndex === index ? null : index)}
                className="w-full px-8 py-6 flex items-center justify-between text-left"
              >
                <div className="flex items-center gap-4">
                  <div className={`p-2 rounded-lg transition-colors ${openIndex === index ? "bg-primary text-white" : "bg-neutral-50 text-neutral-400 group-hover:text-primary"}`}>
                    <HelpCircle className="w-5 h-5" />
                  </div>
                  <h3 className={`text-xl font-black tracking-tight ${openIndex === index ? "text-primary" : "text-secondary"}`}>
                    {faq.question}
                  </h3>
                </div>
                <motion.div
                  animate={{ rotate: openIndex === index ? 180 : 0 }}
                  className={`p-2 rounded-full ${openIndex === index ? "bg-primary/10 text-primary" : "text-neutral-300"}`}
                >
                  <ChevronDown className="w-5 h-5" />
                </motion.div>
              </button>

              <AnimatePresence>
                {openIndex === index && (
                  <motion.div
                    initial={{ height: 0, opacity: 0 }}
                    animate={{ height: "auto", opacity: 1 }}
                    exit={{ height: 0, opacity: 0 }}
                    transition={{ duration: 0.4, ease: [0.16, 1, 0.3, 1] }}
                  >
                    <div className="px-8 pb-8 pt-2 ml-12">
                      <div className="h-0.5 w-12 bg-primary/20 mb-6" />
                      <p className="text-neutral-500 leading-relaxed text-lg font-medium">
                        {faq.answer}
                      </p>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Background elements */}
      <div className="absolute top-1/2 left-0 w-64 h-64 bg-primary/5 rounded-full blur-[100px] pointer-events-none" />
      <div className="absolute bottom-0 right-0 w-96 h-96 bg-secondary/5 rounded-full blur-[120px] pointer-events-none" />
    </section>
  );
}
