'use client';

import React from "react";
import {
  Truck,
  RefreshCcw,
  ShieldCheck,
  Heart,
  Package,
  Star,
  Percent,
  Zap,
} from "lucide-react";
import { motion } from "framer-motion";

const GooglePay = "/assets/frontend_assets/ShippingDetails/googlePay.png";
const Paytm = "/assets/frontend_assets/ShippingDetails/paytm.png";
const upi = "/assets/frontend_assets/ShippingDetails/bhimIcon.png";
const visa = "/assets/frontend_assets/ShippingDetails/visa.png";
const masterCard = "/assets/frontend_assets/ShippingDetails/mastercard.png";
const Bank = "/assets/frontend_assets/ShippingDetails/bank.png";
const Razorpay = "/assets/frontend_assets/ShippingDetails/Razorpay.jpg";

const ShippingDetails = () => {
  const paymentMethods = [
    { name: "Google Pay", image: GooglePay },
    { name: "Paytm", image: Paytm },
    { name: "UPI", image: upi },
    { name: "Visa", image: visa },
    { name: "MasterCard", image: masterCard },
    { name: "Bank Transfer", image: Bank },
    { name: "Razorpay", image: Razorpay },
  ];

  return (
    <section className="relative py-32 overflow-hidden font-[Poppins]">
      <div className="max-w-7xl mx-auto px-6 relative z-10">
        {/* Brand Promise */}
        <motion.div
          initial={{ opacity: 0, scale: 0.9 }}
          whileInView={{ opacity: 1, scale: 1 }}
          viewport={{ once: true }}
          className="mb-24 text-center space-y-6"
        >
          <div className="inline-flex items-center justify-center p-4 bg-primary/10 rounded-3xl border border-primary/20 backdrop-blur-md">
            <ShieldCheck className="text-primary" size={40} />
          </div>
          <h2 className="text-5xl md:text-8xl font-black text-secondary tracking-tighter uppercase leading-[0.9]">
            Parkk <span className="text-primary italic">Integrity</span>
          </h2>
          <p className="text-xl text-neutral-500 max-w-3xl mx-auto font-medium leading-relaxed">
            Every unit is calibrated to perfection. If the visual fidelity fails to meet our
            deployment standards, we initiate a full reconciliation protocol.
          </p>
          <div className="flex items-center justify-center gap-4 text-secondary">
            <div className="h-0.5 w-12 bg-primary/30" />
            <span className="font-black text-xs uppercase tracking-[0.3em]">Zero Question Policy</span>
            <div className="h-0.5 w-12 bg-primary/30" />
          </div>
        </motion.div>

        {/* Benefits Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-24">
          {[
            { title: "ZERO CHARGE LOGISTICS", desc: "Global deployment is factored into the initial protocol.", icon: Truck, color: "primary" },
            { title: "30-DAY RECALIBRATION", desc: "Hassle-free return window for all standard units.", icon: RefreshCcw, color: "success" },
            { title: "NODE LOYALTY", desc: "A 10% credit is applied to all consecutive deployments.", icon: Zap, color: "warning" },
          ].map((benefit, i) => (
            <motion.div
              key={i}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ delay: i * 0.1 }}
              className={`group p-10 rounded-[2.5rem] bg-white/40 backdrop-blur-xl border border-white/50 hover:bg-white/80 transition-all duration-700 hover:shadow-[0_40px_80px_-20px_rgba(0,0,0,0.1)]`}
            >
              <div className={`w-14 h-14 bg-${benefit.color}/10 rounded-2xl flex items-center justify-center mb-8 group-hover:scale-110 group-hover:bg-${benefit.color} group-hover:text-white transition-all duration-500`}>
                <benefit.icon size={28} />
              </div>
              <h3 className="text-2xl font-black text-secondary mb-4 tracking-tight">{benefit.title}</h3>
              <p className="text-neutral-500 font-medium leading-relaxed">{benefit.desc}</p>
            </motion.div>
          ))}
        </div>

        {/* Dynamic Stats */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-8 mb-32">
          {[
            { label: "Synchronized Customers", val: "200k+", icon: Heart },
            { label: "Successful Deployments", val: "200k+", icon: Package },
            { label: "Verified Data Nodes", val: "2730+", icon: Star },
          ].map((stat, i) => (
            <motion.div
              key={i}
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              viewport={{ once: true }}
              className="relative p-10 text-center bg-secondary rounded-[3rem] text-white overflow-hidden group"
            >
              <div className="absolute top-0 left-0 w-full h-full bg-gradient-to-br from-primary/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />
              <stat.icon size={40} className="mx-auto mb-6 text-primary opacity-50 group-hover:scale-110 group-hover:opacity-100 transition-all" />
              <div className="text-5xl font-black mb-2 tracking-tighter">{stat.val}</div>
              <div className="text-[10px] font-black uppercase tracking-[0.2em] opacity-40">{stat.label}</div>
            </motion.div>
          ))}
        </div>

        {/* Secure Payments Cyberpunk Style */}
        <div className="text-center bg-white/40 backdrop-blur-2xl rounded-[4rem] p-16 border border-white/60">
          <div className="inline-flex items-center justify-center mb-8">
            <div className="p-4 bg-success/10 rounded-2xl border border-success/20">
              <ShieldCheck className="text-success" size={32} />
            </div>
          </div>
          <h3 className="text-3xl md:text-5xl font-black text-secondary mb-3 uppercase tracking-tighter">
            Encrypted <span className="text-success">Transactions</span>
          </h3>
          <p className="text-neutral-500 mb-12 text-lg font-medium max-w-2xl mx-auto">
            Our payment relay protocols are locked with end-to-end encryption.
          </p>
          <div className="flex flex-wrap justify-center gap-6 max-w-5xl mx-auto">
            {paymentMethods.map((method, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.05, y: -5 }}
                className="flex items-center justify-center w-32 h-24 rounded-2xl bg-white border border-neutral-100 shadow-sm hover:shadow-xl transition-all duration-300 group overflow-hidden p-4"
              >
                <img
                  src={method.image}
                  alt={method.name}
                  className="max-h-12 w-auto object-contain filter grayscale group-hover:grayscale-0 transition-all"
                />
              </motion.div>
            ))}
          </div>
        </div>
      </div>

      {/* Background visual cues */}
      <div className="absolute top-0 right-0 w-[800px] h-[800px] bg-primary/5 rounded-full blur-[150px] pointer-events-none" />
      <div className="absolute bottom-0 left-0 w-[600px] h-[600px] bg-secondary/5 rounded-full blur-[120px] pointer-events-none" />
    </section>
  );
};

export default ShippingDetails;
