'use client';

import React, { useEffect, useState } from "react";
import Link from "next/link";
import axiosInstance from "@/utils/axiosInstance";
import { motion, AnimatePresence } from "framer-motion";
import { Package, ChevronRight, Sparkles, LayoutGrid, Frame, Monitor, Cpu } from "lucide-react";

const OurCollection = () => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [activeFilter, setActiveFilter] = useState("all");

    useEffect(() => {
        const fetchAllProducts = async () => {
            try {
                const types = ['acrylic', 'canvas', 'backlight'];
                const promises = types.map(async (type) => {
                    const res = await axiosInstance.get(`frames/${type}?t=${Date.now()}`);
                    return res.data.map(item => ({ ...item, type }));
                });

                const results = await Promise.all(promises);
                const merged = results.flat();
                setProducts(merged);
                setLoading(false);
            } catch (error) {
                console.error("Failed to fetch products:", error);
                setProducts([]);
                setLoading(false);
            }
        };

        fetchAllProducts();
    }, []);

    const displayedProducts = activeFilter === "all"
        ? products
        : products.filter(p => p.type === activeFilter);

    const categories = [
        { id: "all", label: "Master Core", icon: <LayoutGrid className="w-4 h-4" /> },
        { id: "acrylic", label: "Optical Acrylic", icon: <Sparkles className="w-4 h-4" /> },
        { id: "canvas", label: "Fiber Canvas", icon: <Frame className="w-4 h-4" /> },
        { id: "backlight", label: "Photon Backlight", icon: <Monitor className="w-4 h-4" /> },
    ];

    return (
        <section className="py-32 bg-transparent font-[Poppins] relative">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                {/* Header Section */}
                <div className="flex flex-col md:flex-row md:items-end justify-between mb-20 gap-8">
                    <div className="space-y-6">
                        <div className="inline-flex items-center gap-3 px-4 py-1.5 bg-primary/10 border border-primary/20 text-primary rounded-full text-[10px] font-black uppercase tracking-[0.3em]">
                            <Cpu className="w-3 h-3 animate-pulse" />
                            <span>System Architecture</span>
                        </div>
                        <h2 className="text-5xl md:text-8xl font-black text-secondary leading-[0.9] tracking-tighter uppercase">
                            Core <br /> <span className="text-primary italic">Intelligence</span>
                        </h2>
                    </div>

                    <Link
                        href="/products"
                        className="group flex items-center gap-4 text-secondary font-black text-sm uppercase tracking-[0.2em] border-b-2 border-primary/20 pb-2 hover:border-primary transition-all"
                    >
                        Full Directory
                        <ChevronRight className="w-5 h-5 group-hover:translate-x-2 transition-transform" />
                    </Link>
                </div>

                {/* Cyber Filter Tabs */}
                <div className="flex flex-wrap gap-4 mb-20">
                    {categories.map((cat) => (
                        <button
                            key={cat.id}
                            onClick={() => setActiveFilter(cat.id)}
                            className={`flex items-center gap-3 px-8 py-4 rounded-xl font-black text-[10px] uppercase tracking-widest transition-all duration-500 border-2 ${activeFilter === cat.id
                                    ? "bg-secondary text-white border-secondary shadow-2xl shadow-secondary/40 -translate-y-1"
                                    : "bg-white/50 backdrop-blur-md text-neutral-500 border-neutral-100 hover:border-primary/30"
                                }`}
                        >
                            {cat.icon}
                            {cat.label}
                        </button>
                    ))}
                </div>

                {/* Matrix Grid */}
                {loading ? (
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-10">
                        {[...Array(4)].map((_, i) => (
                            <div key={i} className="aspect-[4/5] bg-neutral-200/20 animate-pulse rounded-3xl" />
                        ))}
                    </div>
                ) : (
                    <motion.div
                        layout
                        className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-10"
                    >
                        <AnimatePresence mode="popLayout">
                            {displayedProducts.map((product, index) => (
                                <motion.div
                                    key={product.id || index}
                                    layout
                                    initial={{ opacity: 0, scale: 0.9, y: 20 }}
                                    animate={{ opacity: 1, scale: 1, y: 0 }}
                                    exit={{ opacity: 0, scale: 0.9, y: 20 }}
                                    transition={{ duration: 0.5, ease: [0.16, 1, 0.3, 1] }}
                                >
                                    <Link
                                        href={`/shop/${product.type}/${product.shape.toLowerCase()}`}
                                        className="group relative flex flex-col h-full bg-white/40 backdrop-blur-xl rounded-[2.5rem] border border-white/50 hover:bg-white/80 hover:shadow-[0_40px_80px_-20px_rgba(0,0,0,0.1)] transition-all duration-700"
                                    >
                                        <div className="relative aspect-[4/5] overflow-hidden rounded-[2rem] m-3">
                                            <img
                                                src={product.image || "https://via.placeholder.com/400?text=No+Image"}
                                                alt={product.title}
                                                className="w-full h-full object-cover transition-transform duration-1000 group-hover:scale-110"
                                            />
                                            <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />

                                            <div className="absolute top-4 left-4">
                                                <span className="px-4 py-1.5 bg-black/80 backdrop-blur-xl rounded-full text-[8px] font-black uppercase tracking-widest text-white border border-white/10">
                                                    {product.type}
                                                </span>
                                            </div>

                                            <div className="absolute bottom-6 left-6 right-6 translate-y-4 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-500">
                                                <div className="w-full py-4 bg-white text-secondary font-black text-[10px] uppercase tracking-[0.2em] rounded-2xl text-center shadow-2xl">
                                                    Initiate Setup
                                                </div>
                                            </div>
                                        </div>

                                        <div className="p-8 pt-4">
                                            <div className="flex justify-between items-center mb-6">
                                                <h3 className="font-black text-secondary text-xl line-clamp-1 uppercase tracking-tighter">
                                                    {product.title.split(' ')[0]}
                                                </h3>
                                                <div className="h-2 w-2 rounded-full bg-primary animate-pulse" />
                                            </div>
                                            <div className="flex items-center justify-between mt-auto pt-6 border-t border-secondary/5">
                                                <div className="flex flex-col">
                                                    <span className="text-[8px] text-neutral-400 font-black uppercase tracking-[0.2em]">Deployment Cost</span>
                                                    <span className="text-2xl font-black text-secondary group-hover:text-primary transition-colors">
                                                        ₹{product.sizes?.[0]?.price || 0}
                                                    </span>
                                                </div>
                                                <ChevronRight className="w-5 h-5 text-neutral-300 group-hover:text-primary transition-all group-hover:translate-x-2" />
                                            </div>
                                        </div>
                                    </Link>
                                </motion.div>
                            ))}
                        </AnimatePresence>
                    </motion.div>
                )}
            </div>
        </section>
    );
};

export default OurCollection;
