'use client';

import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";
import { ArrowRight, Sparkles, Zap, Activity, Cpu } from "lucide-react";
const parkvideo = "/assets/frontend_assets/HomeSlides/photoparkk_Video.mp4";

export default function LandingPage() {
  const [currentOffer, setCurrentOffer] = useState(0);

  const offers = [
    "NEURAL PHOTOGRAPHY CORE - VERSION 2.0",
    "TRANSFORMING MEMORIES INTO DIGITAL ARTIFACTS",
    "CURATED PHOTON RECEPTORS FOR LONG-TERM STORAGE",
    "PRIORITY DEPLOYMENT ON ALL PROTOCOLS > ₹999",
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentOffer((prev) => (prev + 1) % offers.length);
    }, 4000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="relative min-h-screen overflow-hidden font-[Poppins]">
      {/* Enhanced Offers Bar - Tech/Terminal Style */}
      <div className="relative bg-[#050505] py-2 sm:py-3 overflow-hidden border-b border-primary/20">
        <div className="absolute inset-0 bg-[linear-gradient(90deg,transparent_0%,rgba(255,59,48,0.05)_50%,transparent_100%)] animate-pulse" />
        <div className="relative flex flex-col sm:flex-row items-center justify-center px-4 sm:px-0 gap-2 sm:gap-6">
          <div className="flex items-center gap-3">
            <motion.div
              animate={{ rotate: 360 }}
              transition={{ duration: 4, repeat: Infinity, ease: "linear" }}
              className="text-primary"
            >
              <Cpu size={14} />
            </motion.div>
            <AnimatePresence mode="wait">
              <motion.div
                key={currentOffer}
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -20 }}
                transition={{ duration: 0.5 }}
                className="text-neutral-400 font-mono text-[10px] sm:text-xs tracking-[0.2em] text-center uppercase"
              >
                {offers[currentOffer]}
              </motion.div>
            </AnimatePresence>
          </div>
          <Link
            href="/offers"
            className="group relative px-4 py-1 rounded-sm text-[8px] font-black tracking-widest text-primary border border-primary/30 hover:bg-primary hover:text-white transition-all overflow-hidden"
          >
            <span className="relative z-10">INITIATE_UPGRADE</span>
            <motion.div className="absolute inset-x-0 bottom-0 h-0.5 bg-primary group-hover:h-full transition-all duration-300 -z-0" />
          </Link>
        </div>
      </div>

      {/* Main Landing Section */}
      <div className="relative w-full h-screen">
        {/* Video Background */}
        <div className="absolute inset-0">
          <video
            autoPlay
            loop
            muted
            playsInline
            className="w-full h-full object-cover"
          >
            <source src={parkvideo} type="video/mp4" />
          </video>

          {/* Futuristic HUD Overlay */}
          <div className="absolute inset-0 bg-black/60 backdrop-blur-[2px]" />
          <div className="absolute inset-0 bg-[radial-gradient(circle_at_center,transparent_0%,rgba(0,0,0,0.8)_100%)]" />

          {/* Animated Grid Decoration */}
          <div className="absolute inset-0 opacity-20 pointer-events-none" style={{ backgroundImage: "radial-gradient(#ffffff 0.5px, transparent 0.5px)", backgroundSize: "30px 30px" }} />

          {/* HUD Corner Elements */}
          <div className="absolute top-20 left-20 w-32 h-32 border-t border-l border-white/20 pointer-events-none" />
          <div className="absolute top-20 right-20 w-32 h-32 border-t border-r border-white/20 pointer-events-none" />
          <div className="absolute bottom-20 left-20 w-32 h-32 border-b border-l border-white/20 pointer-events-none" />
          <div className="absolute bottom-20 right-20 w-32 h-32 border-b border-r border-white/20 pointer-events-none" />
        </div>

        {/* Content */}
        <div className="relative z-10 h-full flex items-center">
          <div className="w-full max-w-7xl mx-auto px-8 flex flex-col items-center sm:items-start">

            <motion.div
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              className="max-w-3xl text-center sm:text-left"
            >
              <div className="inline-flex items-center gap-4 mb-8">
                <div className="h-0.5 w-12 bg-primary" />
                <span className="text-primary font-black text-xs uppercase tracking-[0.5em]">SYSTEM_ESTABLISHED_1996</span>
              </div>

              <motion.h1
                initial={{ opacity: 0, x: -50 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 1, delay: 0.3 }}
                className="text-6xl sm:text-9xl font-black text-white mb-8 leading-[0.8] tracking-tighter uppercase"
              >
                Visionary <br />
                <span className="text-primary italic relative">
                  Artifacts
                  <div className="absolute -bottom-4 left-0 w-full h-2 bg-primary/20" />
                </span>
              </motion.h1>

              <motion.p
                initial={{ opacity: 0 }}
                animate={{ opacity: 0.6 }}
                transition={{ duration: 1, delay: 0.6 }}
                className="text-xl sm:text-2xl text-white mb-16 leading-relaxed max-w-2xl font-medium tracking-tight"
              >
                Augment your physical space with precision-engineered
                photography modules. Next-generation clarity for timeless data.
              </motion.p>

              <div className="flex flex-col sm:flex-row gap-8 items-center">
                <Link href="/products">
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="group relative bg-white text-black px-12 py-6 rounded-sm font-black text-lg flex items-center gap-6 overflow-hidden"
                  >
                    <span className="relative z-10">ACCESS_CORE_DATABASE</span>
                    <ArrowRight className="relative z-10 group-hover:translate-x-2 transition-transform" />
                    <div className="absolute inset-0 bg-primary opacity-0 group-hover:opacity-10 transition-opacity" />
                  </motion.button>
                </Link>

                <div className="flex gap-4">
                  {[Sparkles, Activity, Zap].map((Icon, i) => (
                    <div key={i} className="w-12 h-12 rounded-full border border-white/20 flex items-center justify-center text-white/50">
                      <Icon size={18} />
                    </div>
                  ))}
                </div>
              </div>

              {/* Advanced HUD Stats */}
              <div className="grid grid-cols-3 gap-12 mt-24 pt-12 border-t border-white/10">
                {[
                  { label: "UNITS_DEPLOYED", val: "200K+" },
                  { label: "SYNC_STABILITY", val: "99.9%" },
                  { label: "NODE_REVIEWS", val: "2.7K" },
                ].map((s, i) => (
                  <div key={i} className="flex flex-col gap-2">
                    <span className="text-white text-3xl font-black tracking-tighter">{s.val}</span>
                    <span className="text-white/30 text-[8px] font-black uppercase tracking-[0.3em] font-mono">{s.label}</span>
                  </div>
                ))}
              </div>
            </motion.div>
          </div>
        </div>

        {/* Floating Scanline Animation */}
        <motion.div
          animate={{ top: ["0%", "100%", "0%"] }}
          transition={{ duration: 10, repeat: Infinity, ease: "linear" }}
          className="absolute left-0 w-full h-[1px] bg-primary/30 z-20 pointer-events-none shadow-[0_0_10px_rgba(255,59,48,0.5)]"
        />
      </div>
    </div>
  );
}
