'use client';

import React, { useEffect, useState } from "react";
import Link from "next/link";
import axiosInstance from "@/utils/axiosInstance";
import { motion, useInView } from "framer-motion";
import { ChevronRight, Package, ArrowRight, Loader2, Sparkles, Monitor, Layers } from "lucide-react";

/**
 * A reusable, premium section with futuristic UI and scroll-reveal effects.
 */
const CategoryShowcase = ({
    type,
    title,
    description,
    badgeColor = "bg-primary",
    accentColor = "text-primary",
    theme = "light",
    variant = "default"
}) => {
    const ref = React.useRef(null);
    const isInView = useInView(ref, { once: true, margin: "-100px" });

    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchProducts = async () => {
            try {
                const res = await axiosInstance.get(`frames/${type}?t=${Date.now()}`);
                setProducts(res.data.slice(0, 8));
                setLoading(false);
            } catch (error) {
                console.error(`Failed to fetch ${type} products:`, error);
                setProducts([]);
                setLoading(false);
            }
        };
        fetchProducts();
    }, [type]);

    const isDark = theme === "dark" || variant === "backlight";

    // Style configuration based on variant
    const getVariantStyles = () => {
        switch (variant) {
            case 'acrylic':
                return {
                    sectionClass: "bg-blue-50/10",
                    cardClass: "bg-white/20 backdrop-blur-xl border-white/30 hover:border-blue-400/50 shadow-[0_8px_32px_0_rgba(31,38,135,0.07)]",
                    imageClass: "rounded-3xl",
                    icon: <Sparkles className="w-5 h-5" />,
                    decor: <div className="absolute top-0 right-0 w-96 h-96 bg-blue-400/5 rounded-full blur-[100px] -mr-48 -mt-48 pointer-events-none" />
                };
            case 'canvas':
                return {
                    sectionClass: "bg-neutral-50/20",
                    cardClass: "bg-white/80 backdrop-blur-sm border-neutral-100 hover:border-orange-500/30",
                    imageClass: "rounded-2xl transform rotate-[-1deg] group-hover:rotate-0 transition-transform duration-500",
                    icon: <Layers className="w-5 h-5" />,
                    decor: <div className="absolute bottom-0 right-10 w-64 h-64 bg-orange-200/5 rounded-full blur-[80px] pointer-events-none" />
                };
            case 'backlight':
                return {
                    sectionClass: "bg-transparent",
                    cardClass: "bg-neutral-900/40 backdrop-blur-2xl border-neutral-800 hover:border-primary/50 group/glow",
                    imageClass: "rounded-2xl",
                    icon: <Monitor className="w-5 h-5" />,
                    decor: (
                        <div className="absolute inset-0 pointer-events-none">
                            <div className="absolute top-1/2 left-1/4 w-[500px] h-[500px] bg-primary/5 rounded-full blur-[120px]" />
                            <div className="absolute bottom-1/4 right-1/4 w-[400px] h-[400px] bg-secondary/5 rounded-full blur-[100px]" />
                        </div>
                    )
                };
            default:
                return {
                    sectionClass: "bg-white",
                    cardClass: "bg-white border-neutral-100",
                    imageClass: "rounded-2xl",
                    icon: <Package className="w-5 h-5" />,
                    decor: null
                };
        }
    };

    const styles = getVariantStyles();

    const containerVariants = {
        hidden: { opacity: 0 },
        visible: {
            opacity: 1,
            transition: {
                staggerChildren: 0.15,
                delayChildren: 0.2
            }
        }
    };

    const itemVariants = {
        hidden: { y: 40, opacity: 0 },
        visible: {
            y: 0,
            opacity: 1,
            transition: { duration: 0.8, ease: [0.16, 1, 0.3, 1] }
        }
    };

    return (
        <section
            ref={ref}
            className={`py-32 md:py-48 relative ${styles.sectionClass} ${isDark ? "text-white" : "text-secondary"} font-[Poppins] overflow-hidden`}
        >
            {styles.decor}

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
                {/* Header Section */}
                <motion.div
                    initial={{ opacity: 0, x: -50 }}
                    animate={isInView ? { opacity: 1, x: 0 } : {}}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                    className="flex flex-col md:flex-row md:items-end justify-between mb-24 gap-12"
                >
                    <div className="space-y-6 max-w-3xl">
                        <div className={`inline-flex items-center gap-3 px-6 py-2 ${badgeColor} bg-opacity-10 ${accentColor} rounded-full text-[10px] font-black uppercase tracking-[0.3em] border border-${accentColor}/20`}>
                            <motion.span
                                animate={{ scale: [1, 1.2, 1] }}
                                transition={{ duration: 2, repeat: Infinity }}
                                className={`w-2 h-2 rounded-full ${badgeColor}`}
                            />
                            Next-Gen {type}
                        </div>
                        <h2 className="text-6xl md:text-8xl font-black leading-[0.9] tracking-tighter uppercase">
                            {title.split(' ')[0]} <br />
                            <span className={`${accentColor} italic relative`}>
                                {title.split(' ').slice(1).join(' ')}
                                <motion.div
                                    initial={{ width: 0 }}
                                    animate={isInView ? { width: "100%" } : {}}
                                    transition={{ duration: 1, delay: 0.5 }}
                                    className={`absolute -bottom-2 left-0 h-1 bg-current opacity-20`}
                                />
                            </span>
                        </h2>
                        <p className={`text-xl md:text-2xl ${isDark ? "text-neutral-400" : "text-neutral-500"} leading-relaxed font-medium max-w-2xl`}>
                            {description}
                        </p>
                    </div>

                    <Link
                        href={`/shop/${type}`}
                        className={`group relative flex items-center gap-4 px-12 py-6 rounded-2xl ${isDark ? "bg-white text-black" : "bg-black text-white"} font-black text-xs uppercase tracking-[0.2em] transition-all duration-500 hover:scale-105 hover:shadow-[0_20px_40px_rgba(0,0,0,0.4)] active:scale-95 overflow-hidden`}
                    >
                        <span className="relative z-10 flex items-center gap-4">
                            Access Protocol
                            <ArrowRight className="w-5 h-5 group-hover:translate-x-2 transition-transform" />
                        </span>
                        <div className="absolute inset-0 bg-primary opacity-0 group-hover:opacity-10 transition-opacity" />
                    </Link>
                </motion.div>

                {/* Grid Section */}
                {loading ? (
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-12">
                        {[...Array(4)].map((_, i) => (
                            <div key={i} className="aspect-[3/4] bg-neutral-200/20 animate-pulse rounded-[2.5rem]" />
                        ))}
                    </div>
                ) : (
                    <motion.div
                        variants={containerVariants}
                        initial="hidden"
                        animate={isInView ? "visible" : "hidden"}
                        className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-12 lg:gap-16"
                    >
                        {products.map((product, index) => (
                            <motion.div key={product.id || index} variants={itemVariants}>
                                <Link
                                    href={`/shop/${type}/${product.shape.toLowerCase()}`}
                                    className={`group relative flex flex-col h-full rounded-[2.5rem] p-4 transition-all duration-700 border ${styles.cardClass} overflow-hidden`}
                                >
                                    {/* Corner Brackets (Futuristic UI) */}
                                    <div className="absolute top-4 left-4 w-4 h-4 border-t-2 border-l-2 border-current opacity-20" />
                                    <div className="absolute top-4 right-4 w-4 h-4 border-t-2 border-r-2 border-current opacity-20" />
                                    <div className="absolute bottom-4 left-4 w-4 h-4 border-b-2 border-l-2 border-current opacity-20" />
                                    <div className="absolute bottom-4 right-4 w-4 h-4 border-b-2 border-r-2 border-current opacity-20" />

                                    {/* Image Wrapper */}
                                    <div className={`relative aspect-[3/4] overflow-hidden mb-8 transition-all duration-1000 ${styles.imageClass}`}>
                                        <img
                                            src={product.image || "https://via.placeholder.com/400?text=No+Image"}
                                            alt={product.title}
                                            className="w-full h-full object-cover transition-transform duration-1000 group-hover:scale-110 group-hover:rotate-1"
                                        />

                                        {/* Futuristic Data Overlay */}
                                        <div className="absolute top-6 left-6 flex flex-col gap-1 opacity-0 group-hover:opacity-100 transition-opacity duration-500">
                                            <div className="flex items-center gap-2 bg-black/40 backdrop-blur-md px-2 py-1 rounded-md border border-white/10 text-[8px] font-mono text-white uppercase tracking-tighter">
                                                <div className="w-1.5 h-1.5 bg-green-500 rounded-full animate-ping" />
                                                Live Sync
                                            </div>
                                            <div className="bg-black/40 backdrop-blur-md px-2 py-1 rounded-md border border-white/10 text-[8px] font-mono text-white opacity-60">
                                                ID: {product.id?.slice(0, 8)}
                                            </div>
                                        </div>

                                        <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-700" />

                                        <div className="absolute bottom-8 left-8 right-8 translate-y-12 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-700 delay-100">
                                            <div className="flex flex-col gap-1">
                                                <span className="text-white/40 text-[8px] font-black uppercase tracking-[0.2em]">Deployment Price</span>
                                                <span className="text-white text-3xl font-black italic tracking-tighter">₹{product.sizes?.[0]?.price}</span>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Content */}
                                    <div className="px-4 pb-4 space-y-4">
                                        <div className="flex items-start justify-between">
                                            <h3 className={`text-2xl font-black ${isDark ? "text-white" : "text-secondary"} group-hover:${accentColor} transition-colors tracking-tight leading-none uppercase`}>
                                                {product.title.split(' ')[0]}
                                            </h3>
                                            <div className={`p-2 rounded-xl ${isDark ? "bg-white/5" : "bg-neutral-50"} ${accentColor} opacity-50`}>
                                                {styles.icon}
                                            </div>
                                        </div>

                                        <div className="flex items-center justify-between pt-2 border-t border-current/5">
                                            <div className="flex flex-col">
                                                <span className={`text-[9px] font-black uppercase tracking-[0.2em] ${isDark ? "text-neutral-500" : "text-neutral-400"}`}>
                                                    Config
                                                </span>
                                                <span className={`text-xs font-bold ${isDark ? "text-neutral-300" : "text-neutral-600"}`}>
                                                    {product.shape}
                                                </span>
                                            </div>
                                            <motion.div
                                                whileHover={{ x: 5, backgroundColor: isDark ? "rgba(255,255,255,1)" : "rgba(0,0,0,1)", color: isDark ? "rgba(0,0,0,1)" : "rgba(255,255,255,1)" }}
                                                className={`w-10 h-10 rounded-full ${isDark ? "bg-white/5" : "bg-neutral-50"} flex items-center justify-center transition-all duration-300`}
                                            >
                                                <ChevronRight className="w-5 h-5" />
                                            </motion.div>
                                        </div>
                                    </div>

                                    {/* Futuristic Glow Effect */}
                                    <div className={`absolute -inset-[1px] rounded-[2.5rem] bg-gradient-to-tr from-transparent via-current to-transparent opacity-0 group-hover:opacity-20 transition-opacity pointer-events-none`} />
                                </Link>
                            </motion.div>
                        ))}
                    </motion.div>
                )}
            </div>
        </section>
    );
};

export default CategoryShowcase;
